body {
	font-family: roboto, Arial, sans-serif;
	font-size: 16px;
	line-height: 1.25;
	min-width: 320px;
	min-height: 320px;
	height: auto; }

body,
html {
	height: auto; }

.reset_list {
	list-style: none;
	li {
		display: inline-block;
		text-indent: 0 !important;
		&:before {
			display: none !important; } } }

h3 {
	font-weight: 700; }

.device_resize_frame {
	border: 1px solid #000;
	height: 100%;
	display: inline-block;
	position: relative;
	margin: 0 auto;
	border-color: $dark-gray;
	border-width: 20px 20px 40px;
	border-style: solid;
	border-radius: 15px;
	&:after {
		content: '';
		left: 0;
		right: 0;
		position: absolute;
		bottom: -31px;
		margin: auto;
		width: 24px;
		height: 24px;
		border-radius: 50%;
		background-color: #333;
		border: solid 2px #444; }
	&-content {
		overflow: hidden;
		min-width: 270px;
		height: 100%;
		resize: horizontal;
		max-width: 870px;
		width: 870px;
		position: relative;
		&:after {
			content: "";
			@include arr(9, 9, #444, br);
			position: absolute;
			bottom: -1px;
			right: -1px;
			pointer-events: none;
			touch-action: none; } }
	&-wrap {
		text-align: center;
		height: calc(100% - 40px); } }

.all_screen_iframe {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0; }

.centr-align {
	text-align: center; }

.default_link {
	background: none !important;
	&:hover {
		text-decoration: underline; } }

.half {
	float: left;
	width: 50%;
	padding: 15px;
	box-sizing: border-box; }

.slide {
	padding: 40px 40px 0;
	user-select: none;
	&:before {
		content: '';
		position: absolute;
		z-index: 20;
		left: 0;
		top: 0;
		height: 100%;
		width: 40px; }
	h2 {
		font-size: 65px;
		margin-bottom: 40px; }
	pre {
		margin-top: 25px;
		display: inline-block;
		vertical-align: middle;
		margin-bottom: .5em;
		text-align: left;
		width: 100%;
		letter-spacing: -1px;
		list-style-position: -1px;
		code {
			font-size: 29px;
			line-height: 1.6;
			margin-left: -50px;
			width: calc(100% + 175px + 112px); } }
	p {
		font-size: 32px;
		line-height: 1.7; }
	ul {
		font-size: 32px;
		line-height: 1.6; } }

.centr-code {
	text-align: center;
	line-height: 625px;
	padding-top: 0;
	pre {
		margin: 0; }
	code {
		font-size: 30px !important; } }

@mixin custom-header($img) {
	background: url("../img/" + $img) 50% 50% no-repeat;
	background-size: cover;
	.shout {
		padding: 0 50px;
		text-align: center;

		&:after {
			display: none; } }
	h2 {
		font-size: 128px !important;
		text-shadow: 3px 4px 10px #000; } }


.cover-frame {
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	// margin: auto
	width: 100%;
	height: 100%;
	position: absolute; }

.overflow-visible {
	overflow: visible; }

.slide::after {
	left: 20px !important;
	bottom: 20px !important;
	font-size: 16px;
	line-height: 1; }

.contacts {
	padding-left: 30px;
	text-align: left;
	&.half {
		padding-top: 100px; }
	p {
		margin-bottom: 0;
		font-size: 36px; } }

.twitter_link {
	position: relative;
	margin-left: 60px;
	color: #1da1f2 !important;
	background: none !important;
	font-size: 50px;
	&:hover {
		background: linear-gradient(to top,currentColor .09em,transparent .09em) repeat-x !important; }
	&:before {
		content: "";
		position: absolute;
		right: 100%;
		margin-left: -10px;
		top: 5px;
		vertical-align: middle;
		width: 50px;
		height: 50px;
		background: url("../img/Twitter-logo.svg") 50% 50% no-repeat;
		background-size: 50px 50px; } }

.slide blockquote::before {
	font-size: 4em; }

.link-slide {
	padding: 30px 30px 0;
	text-align: center;
	// font-size: 60px
	&_header {
		font-size: 73px !important;
		line-height: 1.3 !important;
		font-weight: 700 !important;
		padding-left: 30px; }
	&_link {
		font-size: 65px !important;
		background: none !important;
		&:hover {
			text-decoration: underline; } } }
